import * as db from './db.js' //common
export const request = (params) => {
	uni.showLoading({
		title: ""
	})
	return new Promise((resolve, reject) => {
		wx.request({
			...params,
			success(res) {
				var jsondata = res.data;
				if (jsondata.success) {
					resolve(jsondata);
				} else {
					showError(jsondata)
					resolve(jsondata);
					//then
					// reject(res.data)
				}
			},
			fail(err) {
				showError(err)
				reject(res.data)
			},
			complete() {
				uni.hideLoading();
			}
		})
	})
}
//
const showError = (error) => {
	var that = this
	if (error) {
		let data = error
		const token = db.get("userToken");
		console.log("------------", token)
		console.log("------------", error.status)
		switch (error.status) {
			case 403:
				uni.showToast({
					title: '',
					icon:'none',
					duration: 4000
				});
				break
			case 500:
				if (data.message == "Token") {
					uni.showModal({
						title: '',
						content: '',
						confirmText: '',
						success: function(res) {
							if (res.confirm) {
								db.del('userToken')
								//
								uni.switchTab({
									url: '/pages/views/mine'
								});
							} else if (res.cancel) {
								console.log('');
							}
						}
					})
					// update-end- --- author:scott ------ date:20190225 ---- for:Token----
				}
				break
			case 404:
				uni.showToast({
					title: '!',
					icon:'none',
					duration: 4000
				});
				break
			case 504:
				uni.showToast({
					title: '',
					icon:'none',
					duration: 2000
				});
				break
			case 502:
				uni.showToast({
					title: '',
					icon:'none',
					duration: 2000
				});
				break
			case 401:
				uni.showToast({
					title: '',
					icon:'none',
					duration: 4000
				});
				if (token=='') {
					setTimeout(() => {
						//
						uni.switchTab({
							url: '/pages/views/mine'
						});
					}, 1500)
				}
				break
			default:
				uni.showToast({
					title: data.message,
					icon:'none',
					duration: 4000
				});
				break
		}
	}
	return null
};
