
import * as db from './db.js'
//
const apiBaseUrl = ''
import {request} from '@/config/request.js'
const post = (url, data) => {
	uni.showLoading({
		title: ''
	});
	var header={
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			// 'Content-Type': 'application/x-www-form-urlencoded', //
		}
	header=setHeader(header);
	return request({
		url: apiBaseUrl + url,
		data: data,
		header:header,
		method: 'POST'
	});
}

const get = (url, data) => {
	uni.showLoading({
		title: ''
	});
	var header={
			'Accept': 'application/json',
			'Content-Type': 'application/x-www-form-urlencoded', //
		}
	header=setHeader(header)
	return request({
		url: apiBaseUrl + url,
		data:data,
		header:header,
		method: 'GET'
	});
}
const setHeader = header => {
	let userToken = db.get("userToken");
	if (userToken) {
	  header[ 'X-Access-Token' ] = userToken //  token 
	}
	return header
}

//put
const put = (url, data) => {
	uni.showLoading({
		title: ''
	});
	var header={
			'Accept': 'application/json',
			'Content-Type': 'application/json',
		}
	header=setHeader(header);
	
	return request({
		url: apiBaseUrl + url,
		data: data,
		header:header,
		method: 'PUT'
	});

}
// 
export const uploadImage = (num, callback) => {
	var header={
			'Accept': 'application/json',
			'Content-Type': 'multipart/form-data',
			// 'Content-Type': 'application/x-www-form-urlencoded', //
		}
	header=setHeader(header);
	uni.chooseImage({
		count: num,
		success: (res) => {
			uni.showLoading({
				title: '...'
			});
			let tempFilePaths = res.tempFilePaths
			for (var i = 0; i < tempFilePaths.length; i++) {
				uni.uploadFile({
					url: apiBaseUrl + '/web-api' +'/sys/common/upload',
					filePath: tempFilePaths[i],
					fileType: 'image',
					name: 'file',
					header: header,
					formData: {
						'method': 'images.upload',
						'upfile': tempFilePaths[i]
					},
					success: (uploadFileRes) => {
						console.log(uploadFileRes)
						callback();
					},
					fail: (error) => {
						if (error && error.response) {
							showError(error.response);
						}
					},
					complete: () => {
						setTimeout(function() {
							uni.hideLoading();
						}, 250);
					},
				});
			}
		}
	});
}


export {post,get}
